import {Notification} from "../models/resources";

const state = {
  notifications: []
};

const mutations = {
  SET_NOTIFICATION(state, payload) {
    state.notification = payload.body.data.attributes;
  },
  SET_NOTIFICATIONS(state, payload) {
    state.notifications = payload.body.data.map(it => it.attributes);
  },
  RESET_NOTIFICATIONS(state) {
    state.notifications = []
  }
};

const getters = {
  notification: (state) => state.notification,
  notifications: (state) => state.notifications,
}

const actions = {
  async getNotification({ commit }, payload) {
    commit('SET_NOTIFICATION', await Notification.get(payload));
  },
  async getNotifications({ commit, store }) {
    commit('SET_NOTIFICATIONS', await Notification.query());
  },
  async updateNotification({ commit, store }, payload) {
    await Notification.update({id: payload.id}, {notification: payload});
  },
};

export default {
  state,
  mutations,
  getters,
  actions,
};
