import Vue from "vue";
import Router from "vue-router";
import FrontPage from "@/components/FrontPage";
const SignUp = () => import("@/components/SignUp");
const SignIn = () => import( "@/components/SignIn");
const MyAccount = () => import( "@/components/pages/MyAccount");
const PrivacyPage = () => import("@/components/PrivacyPage");
const NewProduct = () => import("@/components/pages/NewProduct");
import {store} from '../store/store'

Vue.use(Router);

const router = new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      name: "FrontPage",
      meta: {
        layout: "front-layout",
        guest: true,
      },
      component: FrontPage
    },
    {
      path: "/products/new",
      name: "NewProduct",
      meta: {
        layout: "auth-layout",
        guest: false,
      },
      component: NewProduct
    },
    {
      path: "/services/new",
      name: "NewService",
      meta: {
        layout: "auth-layout",
        guest: false,
      },
      component: NewProduct
    },
    {
      path: "/profile",
      name: "MyAccount",
      meta: {
        layout: "auth-layout",
        guest: false,
      },
      component: MyAccount
    },
    {
      path: "/sign_up",
      name: "SignUp",
      meta: {
        layout: "front-layout",
        guest: true,
      },
      component: SignUp
    },
    {
      path: "/sign_in",
      name: "SignIn",
      meta: {
        layout: "front-layout",
        guest: true,
      },
      component: SignIn
    },
    {
      path: "/privacy",
      name: "PrivacyPage",
      meta: {
        layout: "front-layout",
        guest: true,
      },
      component: PrivacyPage
    },
  ]
});


router.beforeEach((to, from, next) => {
  // const lang = to.query.lang || to.params.lang
  // console.log({ lang, to })
  // loadLanguageAsync(lang || defaultLocale).then(() => next())

  const {layout} = to.meta
  store.commit('SET_LAYOUT', layout)

  if (to.matched.some(record => record.meta.guest)) {
    next()
  } else {

    if (Vue.localStorage.get('default_auth_token')) {
      next()
    } else {
      if (Vue.cookie.get('gdprConsented')) {
        next({name: 'SignIn'})
      } else {
        next({name: 'SignUp'})
      }
    }
  }


  next()
})

export default router;