//*/    🔑 𝗔𝗨𝗧𝗛
//      ▬▬▬▬▬▬▬
//
//      ◌ https://github.com/websanova/vue-auth

import VueAuth from "@websanova/vue-auth";
Vue.use(VueAuth, {
  auth: require("@websanova/vue-auth/drivers/auth/bearer.js"),
  http: require("@websanova/vue-auth/drivers/http/vue-resource.1.x.js"),
  router: require("@websanova/vue-auth/drivers/router/vue-router.2.x.js"),
  fetchData: {url: `${railsApi}/auth/user`, method: 'GET', enabled: true},
  refreshData: {url: `${railsApi}/auth/refresh`, method: 'GET', enabled: true, interval: 30},
  registerData: {url: `${railsApi}/auth/register`, method: 'POST', redirect: '/login'},
  loginData: {url: `${railsApi}/auth/login`, method: 'POST', redirect: '/', fetchUser: true},
  tokenExpired: function () {
    return false
  }
});

//*/ // ▬▬▬▬▬▬▬▬▬▬▬▬▬▬▬▬


//*/    🔑 𝗚𝗢𝗢𝗚𝗟𝗘 𝗔𝗨𝗧𝗛
