import VueAnalytics from "vue-analytics";
if (process.env.NODE_ENV !== 'development') {
Vue.use(VueAnalytics, {
  id: "UA-120602036-3",
  beforeFirstHit() {
    Vue.$ga.set("anonymizeIp", true);
  },
  autoTracking: {
    exception: true
  },
  batch: {
    enabled: true,
    amount: 5,
    delay: 2000
  },
  commands: {

  },
  debug: {
    sendHitTask: process.env.NODE_ENV === "production"
  },
  router
});
}

