import { Country } from "../models/resources";
import keyBy from "lodash/keyBy"

// STATE
const state = {
  countries: [],
  countriesPerId: {},
  countryCodeFilters: [],
};

// GETTERS
const getters = {
  countries: (state) => state.countries,
  countriesPerId: (state) => state.countriesPerId,
  countryCodeFilters: (state) => state.countryCodeFilters,
}

const mutations = {
  SET_COUNTRIES(state, payload) {
    state.countries = payload.body.data.map(it => it.attributes);
    state.countriesPerId = keyBy(payload.body.data.map(it => it.attributes), 'id');
  },
  SET_COUNTRY_CODE_FILTERS(state, payload) {
    state.countryCodeFilters = payload
  },
  TOGGLE_COUNTRY_CODE_FILTERS(state, payload) {
    if (payload.event.shiftKey) {
      const idx = state.countryCodeFilters.indexOf(payload.code);
      if (idx !== -1) {
        state.countryCodeFilters.splice(idx, 1)
      } else {
        state.countryCodeFilters.push(payload.code)
      }
    } else if (state.countryCodeFilters.length === 1 && state.countryCodeFilters[0] == payload.code) {
      state.countryCodeFilters = []
    } else {
      state.countryCodeFilters = [payload.code]
    }
  }
};


const actions = {
  getCountries({ commit }) {
    return new Promise(async (resolve, reject) => {
      commit('SET_COUNTRIES', await Country.query());
      resolve()
    });
  },

  toggleCountryCodeFilters({ commit }, payload, tdd) {
    console.log(payload, tdd)
    commit('TOGGLE_COUNTRY_CODE_FILTERS', payload);
  }
};

export default {
  state,
  mutations,
  getters,
  actions,
};
