// STATE
const state = {
  layout: 'front-layout',
};

// GETTERS
const getters = {
  layout: (state) => state.layout,
}

const mutations = {
  SET_LAYOUT(state, payload) {
    state.layout = payload
  },
};


const actions = {
  setLayout({ commit }, payload) {
    commit('SET_LAYOUT', payload);
  },
};

export default {
  state,
  mutations,
  getters,
  actions,
};
