import { City } from "../models/resources";
import Vue from "vue";
import keyBy from "lodash/keyBy"
import groupBy from "lodash/groupBy"

// STATE
const state = {
  costsPerModel: {
    city: {},
    country: {},
  },
};

// GETTERS
const getters = {
  costsPerModel: (state) => state.costsPerModel,
  costsPerCityId: (state, id) => state.costsPerModel.city[id],
  costsPerCountryId: (state, id) => state.costsPerModel.country[id],
}

const mutations = {
  SET_COSTS(state, payload) {
    const costs = payload.body.data.map(it => it.attributes);
    // console.log({costs});
    if (costs[0] && costs[0].model_type) {
      // Vue.set(state.costsPerModel[costs[0].model_type.toLowerCase()], costs[0].model_id, groupBy(costs, 'model_id'));
      Vue.set(state.costsPerModel[costs[0].model_type.toLowerCase()], costs[0].model_id, costs);
    }
  },
};


const actions = {
  getCosts({ commit }, {name}) {
    return new Promise(async (resolve, reject) => {
      commit('SET_COSTS', await City.costOfLiving({name}));
      resolve()
    });
  },

  toggleCountryCodeFilters({ commit }, payload, tdd) {
    console.log(payload, tdd)
    commit('TOGGLE_COST_CODE_FILTERS', payload);
  }
};

export default {
  state,
  mutations,
  getters,
  actions,
};
