import { Scraper } from "../models/resources";

const state = {
  scrapers: [],
};

const mutations = {
  SET_SCRAPERS(state, payload) {
    state.scrapers = payload.body.data.map(it => it.attributes);
  },
};

const getters = {
  scrapers: (state) => state.scrapers,
}

const actions = {
  getScrapers({ commit }) {
    return new Promise(async (resolve, reject) => {
      commit('SET_SCRAPERS', await Scraper.query());
      resolve()
    });
  },
};

export default {
  state,
  mutations,
  getters,
  actions,
};
