import { City } from "../models/resources";
import Vue from "vue";

// STATE
const state = {
  flightRoutesPerCity: {
  },
};

// GETTERS
const getters = {
  flightRoutesPerCity: (state) => state.flightRoutesPerCity,
}

const mutations = {
  SET_FLIGHT_ROUTES(state, payload) {
    const flightRoute = payload.response.body.data.map(it => it.attributes);
    Vue.set(state.flightRoutesPerCity, payload.id, flightRoute);
  },
};


const actions = {
  getFlightRoutes({ commit }, {name, id}) {
    return new Promise(async (resolve, reject) => {
      const response =  await City.flightRoutes({name});
      commit('SET_FLIGHT_ROUTES', {response, id});
      resolve()
    });
  },
};

export default {
  state,
  mutations,
  getters,
  actions,
};
