<template lang="pug">
  .ui--language-switcher.my-auto
    b-dropdown(variant="white")
      template(slot="button-content")
        span.flag-icon(:class="[`flag-icon-${flags[language]}`]")
      li(v-for="lang in languages")
        b-button.my-auto.dropdown-item(
          variant="light"
          @click="setI18nLanguage(lang);saveLanguage(lang);language = lang"
        )
          span.flag-icon(:class="[`flag-icon-${flags[lang]}`]")




</template>

<script>
  import {User} from '@/store/models/resources'
  import {setI18nLanguage, defaultLocale} from '@/setup/i18n-setup'

  export default {
    name: "LanguageSwitcher",
    props: [],
    data() {
      return {
        languages: ['en', 'fr', 'nl'],
        language: defaultLocale,
        flags: {
          en: 'gb',
          fr: 'fr',
          nl: 'nl'
        }
      };
    },
    methods: {
      setI18nLanguage,
      saveLanguage(locale) {
         const {user} = this.$store.getters
         User.update({id: user.id}, {user: Object.assign({}, user, {locale})})
      }
    }
  };
</script>


