//i18n-setup.js
import Vue from 'vue'
import VueI18n from 'vue-i18n'
import axios from 'axios'

import en from '@/lang/en'
import enTags from 'json-loader!yaml-loader!../lang/tags.en.yml'
import fr from '@/lang/fr'

const dateTimeFormats = {
  'nl': {
    mini: {
      month: 'short',
      day: 'numeric'
    },
    short: {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    },
    medium: {
      month: 'long',
      year: 'numeric',
    },
    long: {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      weekday: 'short',
      hour: 'numeric',
      minute: 'numeric',
      hour12: true
    }
  },
  'en': {
    mini: {
      month: 'short',
      day: 'numeric'
    },
    short: {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    },
    medium: {
      month: 'long',
      year: 'numeric',
    },
    long: {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      weekday: 'short',
      hour: 'numeric',
      minute: 'numeric',
      hour12: true
    }
  },
  'fr': {
    mini: {
      month: 'short',
      day: 'numeric'
    },
    short: {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    },
    medium: {
      month: 'long',
      year: 'numeric',
    },
    long: {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      weekday: 'short',
      hour: 'numeric',
      minute: 'numeric'
    }
  }
}

Vue.use(VueI18n)

let userLang = navigator.language || navigator.userLanguage
if (['en', 'fr', 'nl'].indexOf(userLang) === -1) {
  userLang = null
}

export const defaultLocale =  window.localStorage.getItem('locale') || userLang || 'en'

console.log({enTags})

export const i18n = new VueI18n({
  locale: defaultLocale, // set locale
  fallbackLocale: defaultLocale,
  dateTimeFormats,
  messages: {
    en: Object.assign({}, en, enTags.en),
    fr,
  }
})

export function setI18nLanguage(lang) {
  window.localStorage.setItem('locale', lang)
  i18n.locale = lang

  axios.defaults.headers.common['Accept-Language'] = lang
  document.querySelector('html').setAttribute('lang', lang)

  return lang
}

