import {Attachment} from "../models/resources";
import Vue from 'vue';

const state = {
  attachment: {
    id: null,
    model_id: null,
    model_type: null,
    media: {},
    link: null,
  },
  attachments: []
};

const mutations = {
  SET_ATTACHMENT(state, payload) {
    state.attachment = payload.body.data.attributes;
  },
  SET_ATTACHMENTS(state, payload) {
    state.products = payload.body.data.map(it => it.attributes);
  },
  ADD_ATTACHMENT(state, response) {
    console.log(response)
    state.attachments.push(response.body);
  },
  RESET_ATTACHMENTS(state) {
    state.attachments = []
  }
};

const getters = {
  attachment: (state) => state.attachment,
  attachments: (state) => state.attachments,
}

const actions = {
  resetAttachment({ commit }) {
    commit('RESET_ATTACHMENTS');
  },
  setAttachment({ commit }, payload) {
    commit('SET_ATTACHMENT', payload);
  },
  async getAttachment({ commit }, payload) {
    commit('SET_ATTACHMENT', await Attachment.get(payload));
  },
  async getAttachments({ commit, store }) {
    commit('SET_ATTACHMENTS', await Attachment.query());
  },
  async saveAttachment({ commit, store }, payload) {
    console.log({store, payload})
    const formData = new FormData();
    formData.append('file', payload.file);
    formData.append('attachment[title]', (payload.attachment || {}).title);
    commit('ADD_ATTACHMENT', await Vue.http.post('/v2/attachments', formData,  {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    }));
  },
};

export default {
  state,
  mutations,
  getters,
  actions,
};
