import {Package} from "../models/resources";

const state = {
  package: {
    id: null,
    model_id: null,
    model_type: null,
    category: null,
    title: null,
    description: null,
    kind: null,
  },
  packages: []
};

const mutations = {
  SET_PACKAGE(state, payload) {
    state.package = payload.body.data.attributes;
  },
  SET_PACKAGES(state, payload) {
    state.products = payload.body.data.map(it => it.attributes);
  },
  ADD_PACKAGE(state, response) {
    console.log(response)
    state.packages.push(response.body);
  },
  RESET_PACKAGES(state) {
    state.packages = []
  }
};

const getters = {
  package: (state) => state.package,
  packages: (state) => state.packages,
}

const actions = {
  resetPackage({ commit }) {
    commit('RESET_PACKAGES');
  },
  setPackage({ commit }, payload) {
    commit('SET_PACKAGE', payload);
  },
  async getPackage({ commit }, payload) {
    commit('SET_PACKAGE', await Package.get(payload));
  },
  async getPackages({ commit, store }) {
    commit('SET_PACKAGES', await Package.query());
  },
  async savePackage({ commit, store }, payload) {
    commit('ADD_PACKAGE', await Package.save({package: payload}));
  },
};

export default {
  state,
  mutations,
  getters,
  actions,
};
