import {Product} from "../models/resources";

const state = {
  product: {
    id: null,
    title: null,
    description: null,
    attachments: [],
    is_listed: false,
  },
  products: []
};

const mutations = {
  SET_PRODUCT(state, payload) {
    state.product = payload.body.data.attributes;
  },
  SET_PRODUCTS(state, payload) {
    state.products = payload.body.data.map(it => it.attributes);
  },
};

const getters = {
  product: (state) => state.product,
}

const actions = {
  setProduct({ commit }, payload) {
    commit('SET_PRODUCT', payload);
  },
  async getProduct({ commit }, payload) {
    commit('SET_PRODUCT', await Product.get(payload));
  },
  async getProducts({ commit, store }) {
    // TODO: retrieve filters
    console.log({store})
    commit('SET_PRODUCTS', await Product.query());
  },
};

export default {
  state,
  mutations,
  getters,
  actions,
};
