import { City } from "../models/resources";
import times from 'lodash/times';
// STATE
const state = {
  myLocation: {
    latitude: 0,
    longitude: 0,
    pricePerSquareMeter: []
  },
  pricesPerCity: {

  },
  sharedPriceSteps: [],
  foreignCountries: [
    {
      name: '',
      latitude: 0,
      longitude: 0,
      picked: false,
      pricePerSquareMeter: [],
      priceSteps: []
    }
  ],
};

// GETTERS
const getters = {
  myLatitude: state => state.myLocation.latitude,
  myLongitude: state => state.myLocation.longitude,
  latitude: state => state.foreignCountries[0].latitude,
  longitude: state => state.foreignCountries[0].longitude,
  pricePerSquareMeter: state => state.foreignCountries[0].pricePerSquareMeter,
  priceSteps: state => state.foreignCountries[0].priceSteps,
  sharedPriceSteps: state => state.sharedPriceSteps,
  pricesPerCity: state => state.pricesPerCity
}

const mutations = {
  SET_MY_LATITUDE(state, payload) {
    state.myLocation.latitude = payload
  },
  SET_MY_LONGITUDE(state, payload) {
    state.myLocation.longitude = payload
  },
  SET_LATITUDE(state, payload) {
    state.foreignCountries[0].latitude = payload
  },
  SET_LONGITUDE(state, payload) {
    state.foreignCountries[0].longitude = payload
  },
  SET_PRICE_PER_SQUARE_METER(state, {body, name, id}) {
    if (!state.pricesPerCity[name]) {
      Vue.set(state.pricesPerCity, name, {pricePerSquareMeter: [], priceSteps: []});
    }

    state.pricesPerCity[name].pricePerSquareMeter = body.data.map(it => it.attributes)
    state.pricesPerCity[name].pricePerSquareMeter.forEach(price => {
      price.avg = parseInt(price.avg)
    })

    // const step = parseInt(state.foreignCountries[0].pricePerSquareMeter.length/6)
    state.pricesPerCity[name].priceSteps = []
    const sorted = state.pricesPerCity[name].pricePerSquareMeter.map(price => price.avg).sort((a, b) => a - b)

    const step = parseInt(sorted.length/6);
    console.log({step, sorted});
    // console.log({allPrices});

    state.pricesPerCity[name].priceSteps.push({ start: 0, end: sorted[step-1] });
    state.pricesPerCity[name].priceSteps.push({ start: sorted[step-1], end: sorted[step*2-1] });
    state.pricesPerCity[name].priceSteps.push({ start: sorted[step*2-1], end: sorted[step*3-1] });
    state.pricesPerCity[name].priceSteps.push({ start: sorted[step*3-1], end: sorted[step*4-1] });
    state.pricesPerCity[name].priceSteps.push({ start: sorted[step*4-1], end: sorted[step*5-1] });
    state.pricesPerCity[name].priceSteps.push({ start: sorted[step*5-1], end: sorted[sorted.length-1] });
    


    // SHARED
    state.sharedPriceSteps = []
    let allPrices = []
    for (const key in state.pricesPerCity) {
      allPrices = allPrices.concat(state.pricesPerCity[key].pricePerSquareMeter.map(price => price.avg));
    }
    allPrices = allPrices.sort((a, b) => a - b);


    const sharedStep = parseInt(allPrices.length/6);
    console.log({allPrices, sharedStep});

    state.sharedPriceSteps.push({ start: 0, end: allPrices[sharedStep-1] });
    state.sharedPriceSteps.push({ start: allPrices[sharedStep-1], end: allPrices[sharedStep*2-1] });
    state.sharedPriceSteps.push({ start: allPrices[sharedStep*2-1], end: allPrices[sharedStep*3-1] });
    state.sharedPriceSteps.push({ start: allPrices[sharedStep*3-1], end: allPrices[sharedStep*4-1] });
    state.sharedPriceSteps.push({ start: allPrices[sharedStep*4-1], end: allPrices[sharedStep*5-1] });
    state.sharedPriceSteps.push({ start: allPrices[sharedStep*5-1], end: allPrices[allPrices.length-1] });

    // const min = sorted[0]
    // const max = sorted[sorted.length-1]
    // console.log({min, max})
    // const gap = parseInt((max-min)/6);
    // let start = min
    //
    // times(6, (i) => {
    //   console.log('i ', i);
    //   console.log('start ', start);
    //   console.log('end ', start+gap);
    //   // state.foreignCountries[0].priceSteps.push(sorted[step*(i+1)])
    //   state.foreignCountries[0].priceSteps.push({
    //     start:  Math.floor(start),
    //     end: Math.ceil(start+gap)
    //   })
    //   start += gap;
    // })
  },
};


const actions = {
  setMyLatLng({ commit }, {latitude, longitude}) {
    commit('SET_MY_LATITUDE', latitude);
    commit('SET_MY_LONGITUDE', longitude);
  },
  setLatLng({ commit }, {latitude, longitude}) {
    commit('SET_LATITUDE', latitude);
    commit('SET_LONGITUDE', longitude);
  },
  async setPricePerSquareMeter({ commit }, {name, id}) {
    const response =  await City.pricePerSquareMeter({name, id})

    commit( 'SET_PRICE_PER_SQUARE_METER', {body: response.body, name, id})
  }
};

export default {
  state,
  mutations,
  getters,
  actions,
};
