<template lang="pug">
  b-dropdown#dropdown-aria.ui--notifications-dropdown.my-auto(
    right
    variant="white"
    size="sm"
    )
    template(slot="button-content")
      i.mdi(:class="{'text-danger': notificationsByStatus.unread && notificationsByStatus.unread.length}") notifications
    b-dropdown-header#dropdown-header-1(
      v-if="notificationsByStatus.unread && notificationsByStatus.unread.length"
    ) {{ $t('notifications.unseen') }}
    li(
      v-for="notification in notificationsByStatus.unread"
    )
      router-link.dropdown-item.is-flex.valign(
        @click.native="setAsRead(notification)"

        :to="notification.link"
        )
        span.mr-3( v-html="$t('notifications.booking_to_confirm', {name: notification.name})" )
        span.text-grey.small.ml-auto
          | {{ $d(new Date(notification.created_at), 'mini') }}
    b-dropdown-header#dropdown-header-2 {{ $t('notifications.seen') }}
    li(
      v-for="notification in notificationsByStatus.read"
    )
      router-link.dropdown-item.text-muted.is-flex.valign(
        :to="notification.link"
      )
        span.mr-3( v-html="$t('notifications.booking_to_confirm', {name: notification.name})" )
        span.text-grey.small.ml-auto
          | {{ $d(new Date(notification.created_at), 'mini') }}
</template>

<script>
  import { mapGetters, mapActions } from "vuex";
  import groupBy from 'lodash/groupBy';


  export default {
    name: "NotificationsDropdown",
    props: [],
    data() {
      return {
      };
    },
    created() {
      this.getNotifications()
    },
    computed: {
      ...mapGetters(['notifications']),
      notificationsByStatus() {
        return groupBy(this.notifications, notification => notification.status)
      }
    },
    methods: {
      ...mapActions(['getNotifications', 'updateNotification']),
      setAsRead(notification) {
        this.updateNotification({
          ...notification,
          status: 'read'
        }).then(() => {
          setTimeout(() => {
            this.getNotifications()
          }, 800)
        })
      }
    }
  };
</script>


<style lang="scss">
  .ui--notifications-dropdown {
  }
</style>
