import {User} from "../models/resources";

const state = {
  user: {
    id: null,
    first_name: null
  },

};

const mutations = {
  SET_UUID(state, payload) {
    state.uuid = payload;
  },
  SET_USER(state, payload) {
    state.user = payload;
  },
};

const getters = {
  user: (state) => state.user,
  uuid: (state) => state.uuid
}

const actions = {
  setUser({ commit }, payload) {
    commit('SET_USER', payload);
  },
  setUuid({ commit }, payload) {
    commit('SET_UUID', payload);
  },
  async setUserFromGoogle({ commit }, payload) {
    return await User.authGoogle(payload)
  },
  async updateUser({ commit, store }, payload) {
    return await User.update({id: payload.id}, {user: payload})
  },
};

export default {
  state,
  mutations,
  getters,
  actions,
};
