import {Subscription} from "../models/resources";

const state = {
  subscriptions: [],
  subscription: {}
};

const mutations = {
  SET_SUBSCRIPTION(state, payload) {
    state.subscription = payload.body.data.attributes;
  },
  SET_SUBSCRIPTIONS(state, payload) {
    state.subscriptions = payload.body.data.map(it => it.attributes);
    state.subscription =  state.subscriptions[0]
  },
  RESET_SUBSCRIPTIONS(state) {
    state.subscriptions = []
  }
};

const getters = {
  subscription: (state) => state.subscription,
  subscriptions: (state) => state.subscriptions,
}

const actions = {
  async getSubscription({ commit }, payload) {
    commit('SET_SUBSCRIPTION', await Subscription.get(payload));
  },
  async getSubscriptions({ commit, store }) {
    return commit('SET_SUBSCRIPTIONS', await Subscription.query());
  },
  async updateSubscription({ commit, store }, payload) {
    await Subscription.update({id: payload.id}, {subscription: payload});
  },
};

export default {
  state,
  mutations,
  getters,
  actions,
};
