const settings = {}

console.log({env: process.env})
switch (process.env.NODE_ENV) {
  case 'development':
    settings.railsApi = 'http://localhost:3000/v2'
  case 'preprod':
  case 'staging':
    settings.railsApi = 'http://localhost:3000/v2'
    break
  default:
    settings.railsApi = 'https://news.teachee.io/v2'

}

export const railsApi = settings.railsApi;
